.pagination {
    display: flex;
    justify-content: center;
    flex-direction: row;
    width: 100%;
    padding: 0;
    list-style: none;
}

.pagination li {
    margin-left: 1%;
    padding-left: 1%;
    padding-right: 1%;
    cursor: pointer;
    width: auto;
    height: 25px;
    background: white;
    border: 1px solid;
    display: flex;
    align-items: center;
}

.pagination li:hover {
    background: rgba(32,39,64,1);
    color: white;
}
