@media (max-width: 1140px) {
    .main-content-sort{
        flex-direction: column;
    }
    .main-content-line{
        flex-direction: column;
        height: 100%;
    }

}

@media (max-width: 960px) {
    .main-content-sort{
        margin-top: 1%;
    }

    .main-content-card{
        width: 48%;
        height: 34%;
    }

    .main-content-back{
        height: 100%;
    }
}