.similar-news{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.similar{
    background: rgba(74,139,217,1);
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1%;
}

.similar-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    height: 100%;
    text-align: justify
}


@media (max-width: 540px) {
    .similar-news{
        display: flex;
        flex-direction: column;
    }

    .similar{
        background: rgba(74,139,217,1);
        width: 100%;
        height: 20%;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 1%;
    }

    .similar-container{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        text-align: justify
    }
}