.main-content-back{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.main-content{
    /* background-color: red; */
    width: 90%;
    height: 100%;
    display: flex;
    flex-direction: row;
}

.main-content-block{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.main-content-back-article{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.main-content-article{
    background: rgba(32,39,64,1);
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
}

.main-content-article-img{
    width: 100%;
    height: 100%;
}

.main-content-sort{
    background: rgba(110,98,171,1);
    width: 90%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.main-content-line{
    /* background-color: green; */
    height: 100px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.main-content-card{
    /* background-color: yellow; */
    background: rgba(32,39,64,1);
    width: 540px;
    height: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1%;
}

.main-content-card-style{
    height: 70%;
    width: 100%;
}

.about{
    display: flex;
    justify-content: center;
}