button{
    background: rgba(110,98,171,1);
    border: none;
    color: white;
    text-align: center;
    height: 60%;

    font-size: 20px;
    margin: 20px;
    text-decoration: none;
}

button:hover{
    color: rgb(149, 149, 161);
}

button#sort{
    background: rgba(32,39,64,1);
    border: none;
    color: white;
    text-align: center;
    height: 60%;

    font-size: 20px;
    margin: 20px;
    text-decoration: none;
}

button#sort:hover{
    color: rgb(149, 149, 161);
}

input[type="text"] {
    border: 1px solid white;
    background: #ffffff !important;
    outline: none;
    color: black;
    font-size: 20px;
    height: 52%;
    width: 600px;
}

p{
    font-size: 20px;
    color: white;
}

body{
    margin: 0%;
}