.menu{
    width: 100%;
    height: 75px;
    background: linear-gradient(90deg, rgba(74,139,217,1) 0%, rgba(110,98,171,1) 46%, rgba(32,39,64,1) 100%);
    display: flex;
    justify-content: space-around;
    margin-bottom: 70px;
}

.menu-content{
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    /* background-color: black; */
}

.menu-content-href{
    /* background-color: red; */
    height: 75px;
    width: 20%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 20px;
}

a{
    color: white;
    font-size: 20px;
    padding-right: 10px;
    text-decoration: none;
}

a:link{
    color: white;
}

a:visited{
    color: white;
}

a:hover{
    color: rgb(149, 149, 161);
}

a:active{
    color: white;
}